/* Popup */
/* Popup Styling */
#popupBackground {
  display: block;
  position: fixed;
  overflow-x: hidden;
  overflow-y: scroll;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1040;
  background: #000000;
  background-color: rgba(0, 0, 0, 0.6);
}
/* Popup box */
.popupBox {
  position: relative;
  border-radius: 3px;
  /* Opera 10.5, IE9, Saf5, Chrome, FF4, iOS 4, Android 2.1+ */
  /* useful if you don't want a bg color from leaking outside the border:*/
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  width: 980px;
  display: block;
  margin: 50px auto;
  background: #FFFFFF;
  z-index: 1050;
  -webkit-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.2), 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.2), 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
  -ms-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.2), 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.2), 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
}
.lt-ie9 .popupBox {
  border: 1px solid #E6E6E6;
}
.popupBox.noPanes .cancelButton {
  margin-right: 20px;
}
